export default {
	data() {
		return {
			TabCur: -1,
			scrollLeft: 0,
			TabItems: [{ status: -1, name: '全部' }, { status: 0, name: '待付款' }, { status: 1, name: '待发货' }, { status: 2, name: '待收货' }, { status: 3, name: '待评价' }],
			globalData: {},
			list: {},
			search: ''
		};
	},
	onLoad: function(option) {
		if (option.status > -1) {
			this.TabCur = option.status;
		}
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.status;
			this.scrollLeft = (e.currentTarget.dataset.status - 1) * 60;
			this.loadData();
		},
		loadData() {
			let params = {};
			if (this.TabCur > -1) {
				params.category_id = this.TabCur;
			}
			this.$minApi
				.order(params)
				.then(res => {
					this.list = res;
				})
				.catch(err => {
					console.log(err);
				});
		},
		doPay(id) {
			this.$minApi
				.orderPay({
					order_id: id,
				})
				.then(res => {
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: String(Date.now()),
						nonceStr: res.nonce_str,
						package: 'prepay_id=' + res.prepay_id,
						signType: 'MD5',
						paySign: res.sign,
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
						}
					});
					uni.redirectTo({
						url: '/pages/order/details?id=' + res.order_id
					});
				})
				.catch(err => {
					console.log(err);
				});
		},
		toDetails(id) {
			uni.navigateTo({
				url: '/pages/order/details?id=' + id
			});
		},
		toComment(id) {
			uni.navigateTo({
				url: '/pages/order/comment?order_id' + id
			});
		},
		editStatus(index, status) {
			this.$minApi
				.orderEdit({ id: this.list.data[index].id, status: status })
				.then(res => {
					this.list.data[index] = res.data;
				})
				.catch(err => {
					console.log(err);
				});
		}
	}
};
